.ContactFormSection {
  justify-content: center;
  /* background: url('../../public/images/HeroBGTest.png') center center/cover no-repeat   ; */
  background-color: antiquewhite;
  overflow-x: hidden;
  width: full;
}
.header {
  justify-content: center;
  padding-left: 20%;
  margin: auto;
  margin-right: 30.5%;
  color: rgb(255, 255, 255);
  width: full;
}

.subheadline {
  margin-bottom: 10px;
  padding-left: 20%;
}

.subheadlineContact {
  margin-bottom: 8px;

  padding-left: 00%;
  font-family: "Spectral", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
}
.header > p {
  font-size: 18px; /* need to message me ... */
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.5);

  margin-right: 24.5%;
}

.ContactMeForm {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: full;
  justify-self: center;
  font-size: 16px;
  padding-left: 20%;

  input {
    width: 165%;
    height: 35px;

    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(0, 0, 0);

    &:focus {
      border: 2px solid rgb(116, 204, 184);
    }
  }

  textarea {
    max-width: 165%;
    min-width: 70%;
    width: 165%;
    max-height: 100px;
    min-height: 100px;
    padding: 7px;
    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(220, 220, 220);
    resize: none;

    &:focus {
      border: 2px solid rgba(0, 206, 158, 1);
    }
  }

  label {
    margin-top: 1rem;
    color: rgb(255, 255, 255); /* COLOR OF NAME EMAIL MESSAGE LABEL TEXT */
    font-size: 18px;
    backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.5);
  }

  input[type="submit"] {
    margin-top: 2rem;
    cursor: pointer;
    background: rgb(249, 105, 14);
    color: white;
    border: none;
  }
}

@media screen and (max-width: 1200px) and (min-width: 300px) {
  .header {
    justify-content: center;
    padding-left: 10%;
  }

  .subheadline {
    margin-bottom: 10px;
    padding-left: 10%;
  }

  .ContactMeForm {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 90%;
    justify-self: center;
    font-size: 16px;
    padding-left: 10%;

    input {
      width: 100%;
      height: 35px;

      outline: none;
      border-radius: 5px;
      border: 1px solid rgb(0, 0, 0);

      &:focus {
        border: 2px solid rgb(116, 204, 184);
      }
    }

    textarea {
      max-width: 100%;
      min-width: 70%;
      width: 165%;
      max-height: 100px;
      min-height: 100px;
      padding: 7px;
      outline: none;
      border-radius: 5px;
      border: 1px solid rgb(220, 220, 220);

      &:focus {
        border: 2px solid rgba(0, 206, 158, 1);
      }
    }

    label {
      margin-top: 1rem;
    }

    input[type="submit"] {
      margin-top: 2rem;
      cursor: pointer;
      background: rgb(249, 105, 14);
      color: white;
      border: none;
    }
  }
}
