.hero-container {
  background:
    linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("../../public/images/hexagons.jpeg") no-repeat center center;
  background-size: cover;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.4);
  object-fit: contain;
}

.hero-container > h1 {
  font-size: 100px;
  margin-top: -100px;
}
.headline {
  font:
    90px/1.1em Spectral,
    sans-serif;
  color: rgb(255, 255, 255);

  margin-left: -28%;
  margin-bottom: -6px;
}
.subheadline {
  color: rgb(255, 255, 255);
  font-size: 40px;
  margin-top: -1px;
  /*font-family: "Tangerine Parisienne", cursive ;*/
  font-family: Spectral;
  font-weight: 400;
  font-style: normal;
  margin-bottom: 60px;
  margin-left: -48%;
}

.subheadline-btn {
  color: black;
  font-family: Parisienne, cursive;
  font-weight: 400;
  font-style: normal;
  font-size: 25px;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
  margin-left: 0%;
}

@media screen and (max-width: 1200px) and (min-width: 300px) {
  .hero-container {
    position: relative;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.4);
    object-fit: contain;
  }
  .headline {
    font:
      40px/1.1em Spectral,
      sans-serif;
    color: rgb(255, 255, 255);
    margin-top: 110px;
    margin-left: 1%;
    margin-bottom: -6px;
  }

  .subheadline {
    color: rgb(255, 255, 255);
    font-size: 25px;
    margin-top: -1px;
    /*font-family: "Parisienne", cursive ;*/
    font-family: Tangerine, cursive;
    font-weight: 400;
    font-style: normal;
    margin-bottom: 60px;
    margin-left: -9.5%;
  }

  .hero-btns {
    margin-left: 5%;
  }
}
